export default {
	data() {
		return {
			title: 'Hello',
			uid: '00000',
			username: '工坊用户',
			imghead:'../../static/logo.png',
			wallpaper:null,
			bg_color:null,
			icon_mode:null,
			sec_bg_color:null,
			font_color:null,
			icon_menu_mode:null,
			btn_desc:"登录",
		}
	},
	onShow() {

	/*
	uni.request({
		url:this.websiteUrl+'/user/getUser',
		data:{id:this.id},
		dataType:'json',
		success:res => {
			console.log(res.data);
			this.imghead = res.data.imghead;
			this.uid = res.data.id;
			this.username = res.data.username;
		},
		fail: res => {
			uni.showToast({
				title:'请登录以获得用户资料！',
				icon:'none'
			});
		}
	})*/
	let localuser = uni.getStorageSync('localuser');
	//console.log(localuser);
	if(localuser != null){
		
		
		this.imghead = localuser.imghead;
		this.uid = localuser.id;
		this.username = localuser.username;
		this.wallpaper = localuser.wallpaper;
		this.bg_color = "#"+localuser.color;
		//console.log(this.bg_color);
		this.icon_mode = 'soft-light';
		
		this.btn_desc= "注销";
		
		
	}else{
		this.btn_desc = "登录";
	}


	},
	methods: {
		goto(location) {
			
			if(location==0){
				uni.showToast({
					title: '施工中……',
					icon:"none",
					mask: false,
					duration: 1500
				});
			}
			
			
			uni.navigateTo({
				url: location
			})
		},
		
		doUserAction: function(){
			if(uni.getStorageSync('localuser')==null){
			this.goto('/pages/user/services/login');
			}else{
				uni.setStorageSync('localuser',null);
				
				this.imghead = '../../static/logo.png';
				this.uid = '00000';
				this.username = '工坊用户';
				this.wallpaper = null;
				this.bg_color = null;
				this.icon_mode = null;
				
				
				this.btn_desc = "登录";
				
			}
		}
		
	},
	computed: {

	}
}
